@import url(https://fonts.googleapis.com/css?family=Nunito);
@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans);
.playerCont{width:100%;height:100%;display:flex;flex-direction:column;align-items:center;text-align:center}.playerCont .albumCover{display:flex;position:relative;justify-content:center;align-items:center;width:100%;aspect-ratio:1/1}.playerCont .albumCover img{width:70%;object-fit:cover;aspect-ratio:1/1;border-radius:20px}.playerCont .audioDetails{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;display:flex;flex-direction:column;align-items:center;margin-top:3%}.playerCont .audioDetails .audioTitle{font-size:calc(.6vw + 18px);line-height:calc(.6vw + 20px)}.playerCont .audioDetails .audioArtist{font-size:calc(.4vw + 14px);line-height:calc(.4vw + 16px);font-weight:400;margin-bottom:20px}.playerCont .audioDetails .playPause{width:50px;height:50px;border-radius:1000000px;margin-top:5%}@media(max-width: 768px){.playerCont .albumCover::before{content:"";position:absolute;width:100%;aspect-ratio:1/1;z-index:-1;background-image:var(--background-url);background-size:cover;-webkit-filter:blur(6px);filter:blur(6px)}.playerCont .audioDetails{margin-top:-2%}}@media(min-width: 769px){.playerCont{width:50%;height:100%;padding-top:3vh;display:flex}.playerCont .albumCover{height:60%}.playerCont .albumCover img{height:100%;width:auto;aspect-ratio:1/1}}
.searchBox{background-color:var(--shadow);color:var(--highlight);height:40px !important;border-radius:5px;margin-bottom:3vh}::-webkit-scrollbar{width:6px}::-webkit-scrollbar-track{background:rgba(0,0,0,.1647058824);border-radius:100px}::-webkit-scrollbar-thumb{background:rgba(136,136,136,.3411764706);border-radius:100px}::-webkit-scrollbar-thumb:hover{background:rgba(136,136,136,.6117647059)}.searchContainer{width:100%;height:100%;display:flex;flex-direction:column;align-items:center}.searchContainer .searchBox{margin-top:3vh;width:90%;padding-left:10px}.searchContainer .results{display:flex;flex-direction:row;overflow-y:scroll;scroll-behavior:smooth;flex-wrap:wrap;width:90%;height:calc(100% - (8vh + 40px));justify-content:space-between;margin-bottom:2vh;padding-right:6px}.searchContainer .results .result{width:calc(50% - 10px);margin-bottom:7px;aspect-ratio:1/1 !important;overflow:hidden;border-radius:20px;position:relative}.searchContainer .results .result img{position:absolute;left:0;top:0;width:100%;height:100%;z-index:-1}.searchContainer .results .result .glass{position:absolute;left:0;top:0;width:100%;height:100%;z-index:0;background:linear-gradient(45deg, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.2) 100%)}.searchContainer .results .result .titleBlock{position:absolute;left:0;bottom:0;color:#fff;padding:10px;width:90%}.searchContainer .results .result .titleBlock p{position:relative;width:100%;overflow:hidden;text-overflow:ellipsis;white-space:nowrap;-webkit-transform:translateX(0);transform:translateX(0)}@media(min-width: 769px){.searchContainer{width:50%;height:100%}}@media(max-width: 768px){.searchContainer{transition:600ms ease;position:absolute;top:calc(var(--searchPosition)*(97vh - 60px));left:0;background-color:var(--shadow);z-index:0}}
*{margin:0;padding:0;text-decoration:none;border:none;font-family:"Nunito"}:root{--background: #1D1D1D;--highlight: #fff;--shadow: #191919}body{background-color:#1D1D1D;background-color:var(--background);color:#fff;color:var(--highlight);height:100vh;display:flex;justify-content:center;overflow:hidden}body .App{width:100vw;height:100vh;display:flex;flex-direction:column;overflow:hidden}@media(min-width: 769px){body{align-items:center}body .App{flex-direction:row;width:75vw;height:70vh;justify-content:space-between;background:linear-gradient(45deg, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.2) 100%);border-radius:20px}}
